@import "../../css/share.scss";
.dapp-detail {
    padding-bottom: px(68);
    font-family: PingFang SC;
    box-sizing: border-box;
    background: #171932;
    .dapp-header {
        // border-top: 1px solid #393939;
        border-bottom: 1px solid #393939;
        overflow: hidden;
        .dapp-intro {
            display: flex;
            align-items: stretch;
            padding: px(48) px(34);
            box-sizing: border-box;
            overflow: hidden;
            &>img {
                display: inline-block;
                flex-shrink: 0;
                width: px(147.31);
                height: px(147.31);
                margin-right: px(28);
                border-radius: px(18);
                outline: 1;
                border: none;
                box-shadow: 0 0 0 1px #393939;
            }
            &__info {
                flex-grow: 1;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                padding: px(12) 0;
                span {
                    display: block;
                    width: 100%;
                    font-weight: 500;
                }
                &-name {
                    color: #ffffff;
                    font-size: px(32);
                    @include ellipsis;
                }
                &-price {
                    color: #ffbe81;
                    font-size: px(30);
                }
                &-count {
                    color: #c2c2c2;
                    font-size: px(26);
                }
            }
        }
    }
    .dapp-intro__content {
        padding: 0 px(30);
        min-height: 30vh;
        &-title {
            font-size: px(36);
            font-weight: 500;
            color: #ffffff;
            margin: px(41) 0 px(47);
        }
        &-img {
            width: px(690);
            height: px(329);
            margin: 0 auto;
            box-shadow: 0 0 0 1px #393939;
            border-radius: px(18);
            overflow: hidden;
            img {
                display: block;
                width: 100%;
                height: 100%;
                border: 0;
                outline: 0;
            }
        }
        &-desc {
            height: px(390);
            overflow-x: hidden;
            overflow-y: auto;
            p {
                font-size: px(30);
                color: #c2c2c2;
                line-height: 2;
            }
        }
    }
    .btnBuy {
        display: block;
        width: px(676);
        height: px(92);
        font-size: px(32);
        color: #ffff;
        border: none;
        margin: px(100) auto 0;
        border-radius: px(10);
        background: linear-gradient(270deg, #c48656, #efbe8c);
        &.dsb {
            color: #ffffff;
            background: #c8c8c8;
        }
    }
}